import React, { useState, useEffect } from "react";
import { createCart, getProductCartStatus } from "../../library/parse";
import NumberTextInput from "../../containers/CRUDForm/NumberTextInput";
import { Box } from "grommet";
import Button from "../../components/Button";
import { Chat } from 'grommet-icons'
import Parse from '../../library/parse'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faWhatsapp } from "@fortawesome/free-brands-svg-icons";

const Component = ({ product }) => {
  const [productStatus, setProductStatus] = useState([]);
  const user = Parse.User.current();
  const displayPrice =
    product.get("closePrice") && product.get("closePrice").toFixed(2);
  const [qty, setQty] = useState(0);
  const loadProductCart = () => {
    return getProductCartStatus(product).then((e) => {
      const initQty = product.get("quantity");
      if (e.length > 0) {
        setProductStatus(e);
        setQty(initQty - e[0].get("quantity"));
      } else {
        setQty(initQty);
      }
    });
  };
  const [buyQuantitiy, setBuyQuantity] = useState(1);

  useEffect(() => {
    loadProductCart();
  }, []);

  return (
    <>
      <Box pad="2px">{`$${displayPrice}`}</Box>
      <Box pad="2px">{`Left: ${qty}`}</Box>

      {buyQuantitiy > 1 && (
        <NumberTextInput
          size="small"
          placeholder="buy how many?"
          onChange={(v) => setBuyQuantity(v)}
        />
      )}
      <Box direction="row" gap="small">
      <Button
        focusIndicator
        onClick={() => {
          if(user) {
            return createCart(
              product.id,
              product.get("closePrice"),
              buyQuantitiy
            ).then(() => loadProductCart());
          }

        }}
        label={productStatus.length ? "Sold Out" : "Bid"}
        disabled={productStatus.length}
        size="small"
        primary
      />

      </Box>
      {
        "" && <Box direction="row" gap="small" margin="xsmall">
      <Button 
        background="brand"
        icon={<Chat  size="16px" />}
        primary
      />

      <Button 
        icon={<FontAwesomeIcon icon={faWhatsapp} />}
        primary
      />
      </Box>
      }
    </>
  );
};

export default Component;
